.header {
  margin-top: 20px;
  position: relative;

  @media screen and (max-width: 768px){
    position: static;
    display: flex;
    flex-direction: column;
    //justify-content: center;
  }

  .menu-btn {
    position: absolute;
    top: 12px;
    left: 12px;
    border: none;
    color: $color-3;
    background: $color-1;
    font-size: 1.5rem;
    padding: 3px 10px;
    border-radius: 20px;
    //width: 20px;
    cursor: pointer;

    &:hover {
      background: $color-2;
    }

    i {
      vertical-align: middle;
    }
  }

  h1 {
    font-size: 3.3rem;
    text-shadow: 2px 3px 0px $color-1;
    letter-spacing: 2px;

    @media screen and (max-width: 768px){
      margin-top: 10px;
      text-align: center;
      font-size: 2.4rem;
    }
  }

  .form-container {
    position: absolute;
    top: 20px;
    right: 0;
    text-align: center;

    @media screen and (max-width: 768px){
      position: static;
      margin-top: 14px;
    }
    
    form {
      width: 300px;
      margin: 0 auto;
      display: flex;
      justify-content: space-around;
      
      input[type="text"] {
        //padding: 1px 10px;
        flex-grow: 2;
        border-radius: 4px 0 0 4px;
        border: none;
        font-size: 1.5rem;
        //display: block;
        //margin: 0 auto;
        width: 200px;
        outline: none;
        //text-align: center;
        padding-left: 20px;
        background: #277ca3;
        color: $color-3;
      }

      button[type="submit"] {
        border-radius: 0 4px 4px 0;
        font-size: 1.5rem;
        text-align: center;
        border: none;
        //flex-grow: 1;
        background: #277ca3;
        padding: 0 4px;
        cursor: pointer;
      }

      i {
        vertical-align: middle;
        color: $color-3;
      }
    }
  }

  nav {
    margin-top: 20px;
    font-size: 1.3rem;
    text-align: center;

    @media screen and (max-width: 768px){
      display: none;
      position: absolute;
      left: 12px;
      top: 40px;
      z-index: 10;
      
    }

    ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      //align-items: stretch;
      

      @media screen and (max-width: 768px){
        flex-direction: column;
        flex-wrap: nowrap;
        //background-color: $color-1;
        background: linear-gradient(to right, #1b2838, #2a475e 60%);
        border-radius: 4px;
      }

      a {
        text-decoration: none;
        color: $white;
        border-radius: 4px;
        display: inline-block;
        width: 114px;
        text-align: center;
        //margin-right: 5px;
        padding: 2px 8px;

        &:visited {
          text-decoration: none;
          color: $white;
        }
        &:hover {
          background: $color-2;
        }
      }
      a.nav-active {
        background: $color-1;

        &:hover {
          background: $color-2;
        }
      }
    }
  }
}
