.top-to-btm {
  position: relative;
}

.icon-position {
  position: fixed;
  bottom: 40px;
  right: 25px;
  z-index: 20;
}

.icon-style {
  background: radial-gradient($color-2, $color-1);
  border-radius: 4px;
  height: 40px;
  width: 40px;
  color: $color-3;
  cursor: pointer;
  animation: appear 0.5s ease;
}

.icon-style:hover {
  animation: appear 0.5s ease;
  color: $white;
}

@keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}