.pagination-container {
  margin-top: 14px;

  .pagination-btn {
    margin: 0 auto;
    display: flex;
    max-width: 350px;
    justify-content: center;
    column-gap: 8px;

    button {
      border: none;
      color: $color-3;
      background: $color-1;
      font-size: 1.3rem;
      border-radius: 4px;
      display: inline-block;
      width: 32px;
      text-align: center;
      cursor: pointer;
  
      &:hover {
        background: $color-2;
      }

      i {
        vertical-align: middle;
      }
    }

    span {
      border: none;
      color: $color-3;
      background: transparent;
      font-size: 1.3rem;
      border-radius: 4px;
      display: inline-block;
      width: 32px;
      text-align: center;
    }

    .active {
      color: $white;
    }
  }
}