@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/Dongle-Regular.ttf);
}
@font-face {
  font-family: "font-2";
  src: url(../assets/fonts/Dongle-Light.ttf);
}

$font-1: "font-1", sans-serif;
$font-2: "font-2", sans-serif;

$color-1: #277ca3;
$color-2: #1b2838;
$color-3: #66c0f4;
$black: #060d2c;
$white: rgb(238, 238, 238);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $font-1;
}

body {
  /*background: $color-2;*/
  background: linear-gradient(to right, #2a475e, #1b2838);
  color: $white;
  //margin: auto;
}

li {
  list-style: none;
}

.result-container {
  margin-top: 24px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

